import { GlobalDataLayer } from '@porsche/globaldatalayer';
import { TContextEnvironment, TDataLayer } from '@porsche/globaldatalayer-events/projects/pcom/all/types';
import { Environment, Environments } from '../../entities/environment';
import { splitLocale } from '../../entities/locale';
import { DATA_LAYER_APPLICATION_NAME, DATA_LAYER_APP_ID, EventAction } from '../../utility/constants';
import { isString } from '../../utility/helper';

function getEnvironmentAbbreviation(env: Environment): TContextEnvironment {
  switch (env) {
    case Environments.PRODUCTION:
      return 'P';
    case Environments.PREVIEW:
      return 'PP';
    default:
      return 'K';
  }
}

class GlobalDataLayerService {
  homeNavEventId = /^PAGHomeNav_/;
  gdl!: GlobalDataLayer<TDataLayer>;

  constructor() {
    this.initialise();
  }

  initialise() {
    this.gdl = new GlobalDataLayer();
  }

  pushDataLayerEvent(eventAction: EventAction, eventData?: TDataLayer): void {
    if (eventData) {
      this.gdl.push(eventAction, eventData);
    } else {
      this.gdl.push(eventAction);
    }
  }

  setEnvironment(env: Environment) {
    this.gdl.setDefaultProperties(this.homeNavEventId, {
      context: { environment: getEnvironmentAbbreviation(env) }
    });
  }

  setPageName(pageName?: string) {
    this.gdl.setDefaultProperties(this.homeNavEventId, {
      pageExperience: {
        pageName: pageName
      }
    });
  }

  setUser(userId: string, loginStatus: boolean) {
    this.gdl.setDefaultProperties(this.homeNavEventId, {
      ids: { userId },
      visitor: { loginStatus }
    });
  }

  setLocale(locale: string) {
    try {
      const { country, language } = splitLocale(locale);

      this.gdl.setDefaultProperties(this.homeNavEventId, {
        context: { country: country, language: language }
      });
    } catch (err) {
      // throw the error away
    }
  }

  setDefaultProperties(locale: string, pageName?: string): void {
    let defaultGDLProperties: TDataLayer = {
      context: { applicationId: DATA_LAYER_APP_ID },
      visitor: { loginStatus: false }
    };

    try {
      const { country, language } = splitLocale(locale);

      defaultGDLProperties = {
        ...defaultGDLProperties,
        context: { ...defaultGDLProperties.context, country, language, applicationName: DATA_LAYER_APPLICATION_NAME }
      };
    } catch (err) {
      // throw the error away
    }

    if (isString(pageName)) {
      defaultGDLProperties = { ...defaultGDLProperties, pageExperience: { pageName: pageName } };
    }

    this.gdl.setDefaultProperties(this.homeNavEventId, defaultGDLProperties);
  }
}

export default new GlobalDataLayerService();
