import animationManager from '../../../services/animations/animation-manager';
import { AnimationSequences, AnimationSequencesType } from '../../../utility/constants';
import {
  getAnimatedRoute,
  getCurrentRoute,
  isAnimatingBackward,
  isAnimatingForward,
  isClosing,
  isOpening
} from '../../../services/routing-manager';

export const handleAnimationRouting = (): AnimationRouting => {
  const actions: Record<Operations, () => AnimationRouting> = {
    OPENING: openingAction,
    CLOSING: closingAction,
    FORWARD: forwardAction,
    BACKWARDS: backwardAction,
    UNCHANGED: unchangedAction
  };

  const conditionToExecute = getSingleDrawerAction();

  return actions[conditionToExecute]();
};

type Operations = 'OPENING' | 'CLOSING' | 'FORWARD' | 'BACKWARDS' | 'UNCHANGED';
function getSingleDrawerAction(): Operations {
  if (isOpening()) {
    return 'OPENING';
  }
  if (isClosing()) {
    return 'CLOSING';
  }
  if (isAnimatingForward()) {
    return 'FORWARD';
  }
  if (isAnimatingBackward()) {
    return 'BACKWARDS';
  }
  return 'UNCHANGED';
}

type AnimationRouting = { nextRoute: string; currentRoute: string };

const openingAction = (): AnimationRouting => {
  animationManager.play(AnimationSequences.OPEN_SINGLE_DRAWER);

  return {
    nextRoute: '',
    currentRoute: getCurrentRoute()
  };
};

const closingAction = (): AnimationRouting => {
  animationManager.play(AnimationSequences.CLOSE_SINGLE_DRAWER).then(() => {
    // TODO: Focus on burger button if using the keyboard
  });

  return {
    nextRoute: getCurrentRoute() || '',
    currentRoute: getAnimatedRoute() || ''
  };
};

const forwardAction = (): AnimationRouting => {
  setTimeout(() => {
    animationManager.play(AnimationSequences.TRANSITION_NEXT_LEVEL_SINGLE_DRAWER);
  });

  return {
    nextRoute: getCurrentRoute() ?? '',
    currentRoute: getAnimatedRoute() ?? ''
  };
};

const backwardAction = (): AnimationRouting => {
  if (getCurrentRoute() !== '') {
    setTimeout(() => {
      animationManager.play(AnimationSequences.TRANSITION_PREVIOUS_LEVEL_SINGLE_DRAWER);
    });
  }

  return {
    nextRoute: getAnimatedRoute() ?? '',
    currentRoute: getCurrentRoute() ?? ''
  };
};

const unchangedAction = (): AnimationRouting => {
  return {
    nextRoute: '',
    currentRoute: getCurrentRoute()
  };
};

export const isPlayingSingleDrawerAnimation = (): boolean => {
  try {
    const singleDrawerAnimationSequences: AnimationSequencesType[] = [
      AnimationSequences.OPEN_SINGLE_DRAWER,
      AnimationSequences.CLOSE_SINGLE_DRAWER,
      AnimationSequences.TRANSITION_NEXT_LEVEL_SINGLE_DRAWER,
      AnimationSequences.TRANSITION_PREVIOUS_LEVEL_SINGLE_DRAWER
    ];

    return singleDrawerAnimationSequences.some((sequence) => {
      try {
        return animationManager.isPlaying(sequence);
      } catch {
        return false;
      }
    });
  } catch (e) {
    return false;
  }
};
